













































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import moment from 'moment';

@Component
export default class CStopwatch extends Vue {
  // Data
  private dataMilliLength: number = 4;
  // stop | play | pause
  private dataState: string = 'stop';
  private dataTimerId: any = 0;
  private dataStartMoment: any = moment(new Date());
  private dataElapsedMoment: any = this.dataStartMoment.clone();
  private dataPointDiffs: {
    flag: string,
    hour: number,
    minute: number,
    second: number,
    milli: number,
  }[] = [
    {
      flag: '',
      hour: 0,
      minute: 0,
      second: 0,
      milli: 0,
    },
  ];
  private dataHours: number[] = [];
  private dataMinutesAndSeconds: number[] = [];

  // Watch
  @Watch('dataState')
  onStateChange(newValue: string, oldValue: string): void {
  }
  @Watch('dataElapsedMoment')
  onElapsedMomentChange(newValue: string, oldValue: string): void {
    this.diffDatetime();
  }

  // Lifecycle hook
  created(): void {
    this.initHours();
    this.initMinutesAndSeconds();
  }

  // Methods
  private initHours(): void {
    const HOUR_TERM: number = 24;
    for (let i: number = 0; i < HOUR_TERM; i++) {
      this.dataHours.push(i);
    }
  }
  private initMinutesAndSeconds(): void {
    const MINUTE_AND_SECOND_TERM: number = 60;
    for (let i: number = 0; i < MINUTE_AND_SECOND_TERM; i++) {
      this.dataMinutesAndSeconds.push(i);
    }
  }
  private initPointDiff(): void {
    let pointDiff: {
      // '' | flag | pause
      flag: string,
      hour: number,
      minute: number,
      second: number,
      milli: number,
    } = {
      flag: '',
      hour: 0,
      minute: 0,
      second: 0,
      milli: 0,
    };
    this.dataPointDiffs.length = 0;
    Vue.set(this.dataPointDiffs, 0, pointDiff);
  }
  private startMoment(): void {
    this.dataStartMoment = moment(new Date());
    this.dataElapsedMoment = this.dataStartMoment.clone();
  }
  private restartMoment(): void {
    this.dataStartMoment = moment(new Date());
    this.dataElapsedMoment = this.dataStartMoment.clone();
    this.dataStartMoment.subtract(this.dataPointDiffs[0].hour, 'hours');
    this.dataStartMoment.subtract(this.dataPointDiffs[0].minute, 'minutes');
    this.dataStartMoment.subtract(this.dataPointDiffs[0].second, 'seconds');
    this.dataStartMoment.subtract(this.dataPointDiffs[0].milli, 'milliseconds');
  }
  private traceElapsedMoment(): void {
    this.dataTimerId = setInterval((): void => {
      this.dataElapsedMoment = moment(new Date());
      this.diffDatetime();
    }, 1);
  }
  private ringChime(): void {
    let ringElem: HTMLAudioElement = <HTMLAudioElement>document.getElementById('ring');
    ringElem.currentTime = 0;
    ringElem.play();
  }
  private stopElapsedMoment(): void {
    clearInterval(this.dataTimerId);
  }
  private diffDatetime(): void {
    let nowMoment: any = this.dataElapsedMoment.clone();
    Vue.set(this.dataPointDiffs, 0, this.pickPointUp(nowMoment, ''));
  }
  private pushPointDiff(flag: string): void {
    let nowMoment: any = this.dataElapsedMoment.clone();
    this.dataPointDiffs.push(this.pickPointUp(nowMoment, flag));
  }
  private pickPointUp(nowMoment: any, flag: string): {flag: string, hour: number, minute: number, second: number, milli: number} {
    let pointDiff: {
      flag: string,
      hour: number,
      minute: number,
      second: number,
      milli: number,
    } = {
      flag: flag,
      hour: 0,
      minute: 0,
      second: 0,
      milli: 0,
    };
    let hourDiff: number = nowMoment.diff(this.dataStartMoment, 'hours');
    pointDiff.hour = hourDiff;
    nowMoment.subtract(hourDiff, 'hours');
    let minuteDiff: number = nowMoment.diff(this.dataStartMoment, 'minutes');
    pointDiff.minute = minuteDiff;
    nowMoment.subtract(minuteDiff, 'minutes');
    let secondDiff: number = nowMoment.diff(this.dataStartMoment, 'seconds');
    pointDiff.second = secondDiff;
    nowMoment.subtract(secondDiff, 'second');
    let milliDiff: number = nowMoment.diff(this.dataStartMoment, 'milliseconds');
    pointDiff.milli = milliDiff;
    return pointDiff;
  }
  private listenPlay($event: Event): void {
    if (this.dataState === 'stop') {
      this.initPointDiff();
      this.startMoment();
    } else {// pause
      this.restartMoment();
    }
    this.dataState = 'play';
    this.traceElapsedMoment();
  }
  private listenPause($event: Event): void {
    this.dataState = 'pause';
    this.stopElapsedMoment();
    this.pushPointDiff('pause');
  }
  private listenStop($event: Event): void {
    this.dataState = 'stop';
    this.stopElapsedMoment();
    this.pushPointDiff('stop');
  }
  private listenFlag($event: Event): void {
    this.pushPointDiff('flag');
  }
  private calcLapTime(
    index: number,
    pointDiff: {flag: string, hour: number, minute: number, second: number, milli: number}
  ): string {
    let lapTimePoint: {
      flag: string,
      hour: number,
      minute: number,
      second: number,
      milli: number,
    } = {
      flag: pointDiff.flag,
      hour: pointDiff.hour,
      minute: pointDiff.minute,
      second: pointDiff.second,
      milli: pointDiff.milli,
    };
    if (1 < index) {
      let nowMoment: any = moment(new Date());
      let previousMoment: any = nowMoment.clone();
      previousMoment.add(this.dataPointDiffs[index - 1].hour, 'hours');
      previousMoment.add(this.dataPointDiffs[index - 1].minute, 'minutes');
      previousMoment.add(this.dataPointDiffs[index - 1].second, 'seconds');
      previousMoment.add(this.dataPointDiffs[index - 1].milli, 'milliseconds');
      let presentMoment: any = nowMoment.clone();
      presentMoment.add(pointDiff.hour, 'hours');
      presentMoment.add(pointDiff.minute, 'minutes');
      presentMoment.add(pointDiff.second, 'seconds');
      presentMoment.add(pointDiff.milli, 'milliseconds');
      lapTimePoint.hour = presentMoment.diff(previousMoment, 'hours');
      lapTimePoint.minute = presentMoment.diff(previousMoment, 'minutes');
      lapTimePoint.second = presentMoment.diff(previousMoment, 'seconds');
      lapTimePoint.milli = presentMoment.diff(previousMoment, 'milliseconds');
    }
    return String(lapTimePoint.hour) + ':'
      + this.padZero(lapTimePoint.minute, 2) + ':'
      + this.padZero(lapTimePoint.second, 2) + '.'
      + this.padZero(lapTimePoint.milli, this.dataMilliLength);
  }
  private padZero(number: number, length: number): string {
    return ('0'.repeat(length) + String(number)).slice(-length);
  }
}
